$primary-Color: #030616;
$secondary-color: whitesmoke;
$accent-color: wheat;
@mixin pad {
  @media only screen and (min-width: 800px) {
    @content;
  }
}
@mixin desktop {
  @media only screen and (min-width: 970px) {
    @content;
  }
}
@mixin small {
  @media only screen and (max-width: 400px) {
    @content;
  }
}

.__students{

    .add-container{
        display: flex;
        width: 100%;
        justify-content: flex-end;
        align-items: center;
        gap: .3rem;

        .add{
            margin-right: 1rem;
            position: relative;
            width: fit-content;
            cursor: pointer;
            
            &:hover{
                &::after{
                    content: "";
                    width: 100%;
                    height: 100%;
                    position: absolute;
                    left: 0;
                    right: 0;
                    background-color: rgba(0,0,0,0.07);
                    border-radius: 50%;
                    transform: scale(1.35);
                }

            }
        }
    }
    .search-container {
        display: flex;
        width: 100%;
        padding: 1rem 0.3rem;
        gap: 0.5rem;
        align-items: center;
        // flex-direction: column;
        flex-wrap: wrap;
        margin: 1rem 0;
        @include pad {
          border: 1px solid gainsboro;
          border-radius: 5px;
          flex-wrap: nowrap;
          padding-left: 1rem;
          padding-top: 1rem;
        }
    
        @include desktop {
          padding-right: 1.5rem;
          flex-direction: row;
        }
        @media screen and (max-width: 860px) {
          padding-left: 0.3rem;
        }
        @include small {
          gap: 0.8rem;
          flex-wrap: wrap;
        }
        .input-filter-cont {
          width: 100%;
          border: 1px solid #eee;
          border-color: rgb(118, 118, 118);
          border-radius: 5px;
          padding: 0rem;
          display: flex;
          flex-wrap: nowrap;
          align-items: center;
          padding: 0;
          max-height: 45px;
          // overflow: hidden;
          position: relative;
    
          @include pad {
            width: 100%;
            max-height: 40px;
          }
          @include desktop {
            width: 80%;
          }
          @media screen and (min-width: 1020px) {
            width: 50%;
          }
          @media screen and (min-width: 1050px) {
            width: 30%;
          }
          input {
            border: none;
            background-color: transparent;
            padding: 0rem 0.8rem 0rem 0.5rem;
            outline: none;
            width: 100%;
            height: 42px;
    
            @include small {
              font-size: small;
            }
          }
          .output-container{
            display: flex;
            flex-direction: row;
            width: 100%;
            border-bottom-left-radius: 5px;
            border-bottom-right-radius: 5px;
            border-top: none;
            position: absolute;
            bottom: 0;
            top: 48px;
            z-index: 1;
            height: fit-content;
            max-height: 200px;
            overflow: auto;
            // padding: .5rem;

            &:has(.names){
              border: 1px solid gray;

            }
            & .names{
                // border: 1px solid darken(gainsboro, 2%);
                background-color: white;
                  
                }
            // padding: .5rem;
            @include pad {
              top: 42px;
            }
            .xtra {
              @include desktop {
                width: 24%;
              }
              @include small {
                width: 0;
              }
            }
            .output-names {
              // background-color: #e4e4e4;
              // padding: .2rem 1rem;
              // margin:0 auto;
              width: 100%;
              height: fit-content;
              grid-column: 2;
              // padding: .5rem;
    
              .names {
                margin: 0;
                padding: 0.1rem 0.5rem;
                color: black;
                line-height: 30px;
                cursor: pointer;
                &:hover {
                  background-color: #5499dd;
                  color: white;
                }
    
                span {
                  text-transform: uppercase;
                  font-size: 12px;
                }
              }
            }
          }
        }
        select {
          padding: 0.5rem 0.9rem;
          // margin-bottom: 25px;
          // border: none;
          outline: none;
          width: 25%;
          max-width: 78px;
          min-width: 78px;
          @include desktop {
            max-width: 100%;
            width: fit-content;
          }
          @include small {
            width: 30%;
            max-width: 100%;
            text-align: center;
    
            &:focus {
              text-align: left;
            }
          }
        }
        .btn-cont {
          // width: 30%;
          display: flex;
          gap: 0.5rem;
    
          button {
            transform: scale(0.97);
            display: flex;
            flex-direction: row;
            flex-wrap: nowrap;
            align-items: center;
          }
        }
      }
      .select-cont{
        display: inline-flex;
        width: auto;
        align-items: center;
        gap: .5rem;
        padding: 1rem .8rem;
        margin: .5rem 0;
        border-radius: 5px;
        flex-direction: row;
        flex-wrap: wrap;
        position: relative;
        .close{
          position: absolute;
          right: 0;
          top: 0;
          svg{
              cursor: pointer;
          }
      }
        label {
          width: 35%;

          @include pad{
            width: fit-content;
            padding: 0 .4rem 0 .3rem;
          }
        }
        @include small {
          // flex-wrap: wrap;
          width: 100%;
        }
        .input-cont{
          display: flex;
          gap: .7rem;
          align-items: center;
          flex-direction: row;
          flex-wrap: nowrap;
          width: 80%;

          @include pad{
            width: fit-content;
          }
          @include small{
            width: 80%;
          }

          label{
            width: 50%;
            text-transform: capitalize;

            @include pad{
              width: fit-content;
            }
            @include small{
              width: fit-content;
            }
          }
          select {
            padding: 0.5rem 0.5rem;
            // margin-bottom: 25px;
            border: none;
            outline: none;
            // max-width: 70px;
            border-radius: 5px;
            max-width: 60px;
            @include pad {
              max-width: 100%;
            }
          }
        }
        button{
          margin-right: .4rem;
          @include pad{
            margin-right: 1rem;
          }
        }
      }
      .table-cont {
        width: 100%;
        min-height: 320px;
        overflow: hidden;
        .our-table {
          width: 100%;
          display: grid;
          text-transform: uppercase;
          grid-template-columns: minmax(50px, 1.5fr) minmax(50px, 1fr) 70px;
          grid-gap: 0 0;
          border: 1px solid gainsboro;
          font-size: small;
          overflow: hidden;
          padding: 0;
          @include pad {
            grid-template-columns: 1fr 1fr 75px;
            font-size: initial;
          }
          @include desktop {
            grid-template-columns: 1fr 1fr 75px,
          }
  
          @media screen and (max-width: 350px) {
            grid-template-columns: minmax(50px, 1fr) minmax(30px, 1fr) 40px;
            grid-template-rows: repeat(auto-fit, 45px);
            // grid-gap: 0 1rem;
            // font-size: .7rem;
          }
          @media screen and (max-width: 326px) {
            overflow: auto;
          }
          hr {
            grid-column: 1/4;
            margin: 0 0 0.2rem 0;
          }
          .header {
            font-weight: bold;
            // text-align: center;
            padding: 0.3rem;

            &:first-child{
              margin-right: .5rem;

              select{
                margin-left: .5rem;
                border-radius: 5px;
                padding: 0;
                width: 35px;
                text-align: center;
                outline: none;
              }
            }
           
          }
          .table-row {
            grid-column: 1/4;
          }
          .loading {
            font-size: small;
          }
          details:not([open]) {
            &:nth-child(even) {
              background-color: whitesmoke;
            }
            summary span {
              color: black;
              // padding-left: .5rem;
              &:nth-child(1) {
                font-weight: normal;
              }
            }
          }
          details:has(> summary) summary span {
            // padding-left: 0;
            &:nth-child(1) {
              font-weight: bold;
            }
          }
          details::marker{
            display: none;
          }
          summary {
            grid-column: 1/7;
            display: grid;
            width: 100%;
            grid-template-columns: minmax(50px, 1.5fr) minmax(50px, 1fr) 70px;
            grid-template-rows: auto;
            grid-gap: 0.3rem 0.2rem;
            // font-size: small;
            align-content: center;
  
            &::marker {
              display: none;
            }
            @include pad {
                grid-template-columns: 1fr 1fr 75px;
              grid-gap: 0.3rem 0rem;
            }
            @include desktop {
                grid-template-columns: 1fr 1fr 75px,
              }
      
              @media screen and (max-width: 350px) {
                grid-template-columns: minmax(50px, 1fr) minmax(30px, 1fr) 40px;
                grid-template-rows: repeat(auto-fit, 45px);
                // grid-gap: 0 1rem;
                // font-size: .7rem;
              }
            span {
              padding: .65rem 0.2rem;
              text-overflow: ellipsis;
              white-space: nowrap;
              overflow: hidden;
              display: flex;
              align-items: center;

              @include pad {
                padding: .7rem 0.2rem;
                font-size: small;
              }
              @include desktop {
                padding: .8rem 0.2rem;
              }
              @media screen and (max-width: 350px) {
                padding: 0.4rem 0.2rem;
              }

              input{
                margin: 0 .2rem 0 .05rem;
                min-width: 20px;
              }
  
            }
            span.status{
              span{
                &:first-child{
                  min-width: 50px;
                }
              }
            }
          }
          p {
            width: 100%;
            display: flex;
            flex-direction: column;
            // background-color: lighten(#717790, 30%);
            padding: 0.5rem;
            border-radius: 0;
            // border-bottom-left-radius: 10px;
            // border-bottom-right-radius: 10px;
            font-size: small;
            margin-bottom:.5rem;
            // opacity: 0.5;
            color: black;
            @include pad {
              // background-color: lighten(#c7c4c4, 10%);
              font-size: 0.9rem;
            }
            @media screen and (max-width: 350px) {
              font-size: smaller;
              padding: 0.5rem 0.2rem;
            }
            .details-content {
              display: flex;
              padding-left: 0.5rem;
              gap: 0.5rem;
              width: 100%;
  
              @media screen and (max-width: 350px) {
                flex-direction: column;
                padding: 0;
                gap: 0.1rem;
                border-bottom: 1px solid #000;
              }
              span {
                padding: 0.3rem 0.5rem 0.3rem 0;
  
                @include pad {
                  padding: 0.3rem 0.3rem 0.3rem 0rem;
                }
                &:first-child {
                  width: 25%;
                  min-width: 150px;
                  max-width: 150px;
  
                  @include pad {
                    min-width: 172px;
                    max-width: 172px;
                  }
                }
                @include small {
                  padding-right: 0.8rem;
                  color: lighten(black, 20%);
                }
                @media screen and (max-width: 350px) {
                  width: 60%;
                  padding: 0;
                }
              }
            }
          }
        }
        .our-table::-webkit-scrollbar {
          width: 0.1rem !important;
          height: 0.4rem;
        }
        .our-table::-webkit-scrollbar-track:hover {
          box-shadow: inset 0 0 6px rgba(0, 0, 0, 0.3);
        }
        .our-table::-webkit-scrollbar-thumb:hover {
          background-color: gray;
          outline: none;
        }
        .our-table:has(details[open]) {
          grid-template-rows: auto;
          border: none;
        }
      }
      .pagination-buttons {
        display: flex;
        align-items: center;
        justify-content: flex-end;
        // gap: 1px;
        padding: 0.5rem 0 0;
        button {
          background-color: none;
          background-color: #ddd;
          color: #2c303d;
          height: 35px;
          border-radius: 2px;
          padding: 0 10px;
          text-transform: uppercase;
          letter-spacing: 0.1rem;
          transition: background-color 0.3s ease;
          border: none;
          appearance: none;
          cursor: pointer;
          display: flex;
          align-items: center;
          justify-content: center;
          transform: scale(0.8);
  
          &:not([class$="-page"]) {
            width: 35px;
          }
          &[class$="-page"] {
            background-color: #ccc;
            font-size: 0.5rem;
            font-weight: bold;
          }
        }
        .active {
          background-color: #717790;
          color: #fff;
        }
        button[disabled] {
          opacity: 0.3;
          cursor: default;
        }
      }

    .upload-modal {
        padding: .2rem 0;
        @include pad {
          padding: 1.5rem 0;
        }
        .modal-body {
          overflow: hidden;
          width: 90%;
          top: 0;
      
          @include pad {
            width: 95%;
            border-radius: 10px;
            padding-top: 0.2rem;
            padding-bottom: 0.2rem;
            margin-top: 0.5rem;
          }
          @include desktop {
            // width: 750px;
          }
          @media screen and (max-width: 500px) {
            width: 90%;
          }
          @include small {
            width: 90%;
          }
          .content {
            h4{
              text-align: center;
              font-weight: bold;
            }
            table {
              width: 100%;

              tr {
                text-transform: uppercase;
                font-size: small;
              }
            }
          }
        }
    }
    .student-add-modal {
        padding: .2rem 0;
        @include pad {
          padding: 1.5rem 0;
        }
        .modal-body {
          overflow: hidden;
          width: 70%;
          top: 0;
      
          @include pad {
            width: 600px;
            border-radius: 10px;
            padding-top: 0.2rem;
            padding-bottom: 0.2rem;
            margin-top: 0.5rem;
          }
          @include desktop {
            width: 620px;
          }
          @media screen and (max-width: 500px) {
            width: 90%;
          }
          @include small {
            width: 90%;
          }
          .content {
            form {
              display: flex;
              flex-direction: column;
              // padding: .5rem;
              gap: 0.5rem;
      
              @include pad {
                gap: 1rem;
              }
              .info {
                background-color: #5499dd;
                padding: 1rem;
                border-radius: 5px;
              }
              .input-cont {
                display: flex;
                flex-direction: column;
                gap: 0.3rem;
      
                @include pad {
                  flex-direction: row;
                }
                label {
                  width: 100%;
                  @include small {
                    font-size: small;
                  }
      
                  @include pad {
                    width: 35%;
                  }
                }
                input,
                textarea {
                  font-size: small;
                  padding: 0.6rem;
                  border: none;
                  border-radius: 5px;
                  border: 1px solid gainsboro;
                  outline: none;
                  width: 100%;
                  resize: none;
      
                  &:disabled {
                    // background-color: whitesmoke;
                    border: 1px solid dodgerblue;
      
                    @include pad {
                      padding: 0.6rem;
                    }
                  }
                }
                select {
                  padding: 0.76rem;
                  border: none;
                  border: 1px solid gainsboro;
                  outline: none;
                  width: 100%;
                  font-size: small;
                  border-radius: 5px;
                }
              }
            }
          }
        }
    }
    .edit-modal {
        padding: 1rem 0;
        @include pad {
          padding: 1.5rem 0;
        }
        .modal-body {
          overflow: hidden;
          width: 70%;
          margin-top: 9rem;
          // top: 0;
      
          @include pad {
            width: 600;
            border-radius: 10px;
            padding-bottom: 0.2rem;
            margin-top: 0.5rem;
          }
          @include desktop {
            width: 650px;
          }
          @media screen and (min-width: 760) {
            margin-top: .5rem;
          }
          @media screen and (max-width: 500px) {
            width: 90%;
          }
          @include small {
            width: 90%;

          }
          .content {
            form {
              display: flex;
              flex-direction: column;
              // padding: .5rem;
              gap: 0.5rem;
      
              @include pad {
                gap: 1rem;
              }
              .info {
                background-color: #5499dd;
                padding: 1rem;
                border-radius: 5px;
              }
              .input-cont {
                display: flex;
                flex-direction: column;
                gap: 0.3rem;
      
                @include pad {
                  flex-direction: row;
                }
                label {
                  width: 100%;
                  @include small {
                    font-size: small;
                  }
      
                  @include pad {
                    width: 35%;
                    
                  }
                }
                input,
                textarea{
                  font-size: small;
                  padding: 0.6rem;
                  border: none;
                  border-radius: 5px;
                  border: 1px solid lighten(black, 80%);
                  outline: none;
                  width: 100%;
                  resize: none;
      
                  &:disabled {
                    // background-color: whitesmoke;
                    border: 1px solid dodgerblue;
      
                    @include pad {
                      padding: 0.6rem;
                    }
                  }
                }
               
                select {
                  padding: 0.76rem;
                  border: none;
                  border: 1px solid gainsboro;
                  outline: none;
                  width: 100%;
                  font-size: small;
                  border-radius: 5px;
                }
              }
              .last-input-cont{

                div{
                  width: 100%;
                  .check{
                    width: fit-content;
                    margin-right: .5rem;
                }
                small{
                  font-size: small;
                  color: lighten(black, 50%);
                }
                }
              }
            }
          }
        }
    }
    .search-modal{
      padding: 0;
        @include pad {
          padding: 1.5rem 0;
        }
        .modal-body {
          overflow: hidden;
          width: 80%;
          top: 0;
          margin-top: 1rem;
          margin-bottom: 3.5rem;
          padding: 0.5rem .5rem .7rem;
      
          @include pad {
            width: fit-content;
            border-radius: 10px;
            padding-top: 0.2rem;
            padding-bottom: 0.2rem;
            margin-top: 0.5rem;
          }
          @include desktop {
            width: 700px;
          }
          @media screen and (max-width: 500px) {
            width: 90%;
          }
          @include small {
            width: 90%;
          }

          .content{
            .search-cont{
              .search-container{

                .input-filter-cont{
                  width: 100%;

                  @include pad {
                    width: 100%;
                    max-height: 40px;
                  }
                }
                label{
                  width: 40%;

                  @include small{
                    width: 100%;
                  }
                }
              }
              .output-search{
                height: 100%;
                min-height: 35vh;

                @include pad{
                  min-height: 50vh;
                }
                .tree-container{
                  width: 100%;
                  display: flex;
                  flex-direction: column;
                  flex-wrap: nowrap;
                  padding: 0rem .2rem;
                  border-bottom: 1px solid gray;
                  margin-bottom: .5rem;

                  p {
                    margin-bottom: 0;
                  }
                  &:only-child{
                    border-bottom: none;
                  }
                  .tree-parent{
                    display: flex;
                    width: 100%;
                    flex-direction: column;
                    align-items: center;

                    .parent-label {
                      font-size: small;
                      font-weight: bold;
                      padding: .2rem;
                      text-transform: uppercase;
                    }
                  }
                  .children-cont{
                    display: flex;
                    width: 100%;
                    flex-direction: column;
                    align-items: center;
                    .children-label {
                      font-size: small;
                      font-weight: bold;
                      padding: .5rem .5rem;
                      text-transform: uppercase;
                    }
                    .children-container{
                      margin-top: 1rem;
                      display: grid;
                      grid-template-columns: minmax(100px, 1fr) minmax(100px, 1fr);
                      grid-column-gap: 1rem;
                      grid-row-gap: .5rem;
                      .child:nth-child(odd){
                        border-right: 1px solid gainsboro;
                        border-top: 1px solid gainsboro;
                      }
                      .child:nth-child(even){
                        border-left: 1px solid gainsboro;
                        border-bottom: 1px solid gainsboro;
                      }
                      .child {
                          cursor: pointer;
                          padding: .6rem;
                          border-radius: 5px;
                          &:hover{
                            background-color: rgba(0, 0, 0, 0.3);
                          }
                          @include small{
                            font-size: small;
                          }
                      }
                      .class{
                        font-size: .55rem;
                        padding: 0;

                      }
                    }
                    .child-container{
                      display: grid;
                      grid-template-columns: minmax(100px, 1fr);

                      .class{
                        font-size: smaller;
                        padding: 0;

                      }
                    }
                  }
                }
              }
            }
          }
        }
    }
}